
























import { Component, Vue } from 'vue-property-decorator';

@Component
export default class GoodsFooter extends Vue {
  private gid: string | string[] = '';
  private checkAll: boolean = !1;
  public created() {
    // this.gid = this.$route.query.gid;
  }
  private trade() {
    const list = this.$store.state.cart.list;
    const data: any[] = [];
    for (const item of list) {
      if (item.add) {
        data.push({
          id: item.id,
          num: item.num,
        });
      }
    }
    console.log('trade: ', this.$store.state.cart.amount, data);
  }
  private addAll() {
    const list = this.$store.state.cart.list;
    this.checkAll = !this.checkAll;
    for (const item of list) {
      item.add = this.checkAll;
    }
    if (list.length === 0) {
      this.checkAll = !1;
    }
    this.$store.commit('setCartList', list);
  }
  private deleteAll() {
    const list = this.$store.state.cart.list;
    const arr: number[] = [];
    for (const item of list) {
      arr.push(item.id);
    }
    this.update(arr, []);
  }
  private deleteChecked() {
    const list = this.$store.state.cart.list;
    const arr: number[] = [];
    const lists = list.filter((item) => {
      if (item.add) {
        arr.push(item.id);
      }
      return !item.add;
    });
    if (arr.length === 0) {
      return !1;
    }
    this.update(arr, lists);
  }
  get showDelete() {
    return this.$store.state.cart.manageState;
  }
  get amount() {
    const list = this.$store.state.cart.list;
    this.checkAll = !1;
    let amount = 0;
    let state = list.length > 0 ? !0 : !1;
    for (const item of list) {
      if (item.add) {
        amount += item.price * item.num;
      }
      if (!item.add) {
        state = !1;
      }
    }
    if (state) {
      this.checkAll = !0;
    }
    this.$store.commit('setCartAmount', amount);
    return amount.toFixed(2);
  }
  private update(arr: number[], lists: any[]) {
    this.$store.dispatch('updateCartPage', {update: arr, list: lists});
  }
}
